import Link from '@material-ui/core/Link';
// material
import { experimentalStyled as styled } from '@material-ui/core/styles';
import { Grid, Divider, Container, Typography, IconButton, Box } from '@material-ui/core';
// routes
import { PATH_PAGE } from '../../routes/paths';


export default function MainFooter() {
  return (
    <Box className="footer_bx">
      
      <Container maxWidth="lg" >
      <Grid container spacing={2}>
        <Grid item md={1}></Grid>
        <Grid item xs={12} md={11}>
          
          <Box className="ftr_box">
            <Link href="https://www.twitter.com/treedefi" target="_blank">
              <IconButton aria-label="delete">
                <Box component="img" src="/static/icons/twitter_icon.svg" />
              </IconButton>
            </Link>
            <Link href="https://wwww.youtube.com/treedefi" target="_blank">
              <IconButton aria-label="delete">
                <Box component="img" src="/static/icons/youtub_icon.svg" />
              </IconButton>
            </Link>
            <Link href="https://www.treedefi.medium.com" target="_blank">
              <IconButton aria-label="delete">
                <Box component="img" src="/static/icons/medium_icon.svg" />
              </IconButton>
            </Link>
            <Typography
              component="p"
              variant="body2"
              sx={{
                mt: 3,
                pb: 0,
              }}
            >
               2021 Tree DeFi. All Rights Reserved.
            </Typography>
          </Box>
        </Grid>
      </Grid>

        
      </Container>
    </Box>
  );
}
